.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
html{
  font-size: clamp(14px, 0.83vw, 16px);
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.sansita{
  font-weight: 800;
  font-family: 'Sansita';
  font-style: italic;
  background-color: white;
}
.sansita:nth-of-type(2){
  background-color: #FFF2B1;
}
.sansita:nth-of-type(3){
  background-color: #F5D1FD;
}
.App-link {
  color: #61dafb;
}
body{
  display: flex;
  width: 100vw;
  max-width: 1920px;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: #D5E8FF;
}
.wrapper{
  display: flex;
  width: 100vw;
  max-width: 1920px;
  height: 100vh;
  align-items: center;
  justify-content: center;

}
h2{
  font-weight: 500;
}
.header{
  box-sizing: border-box;
  padding: 0.5rem 0rem;
  background-color: white;
  width: 100vw;
  height: 10rem;
  position: absolute;
  display: flex;
  justify-content: center;
  left: 0;
  z-index: 2;
}
.flex{
  display: flex;
}
.flex-col{
  flex-direction: column;
}
.container{
  width: 100%;
  position: relative;
  display: flex;
  box-sizing: border-box;
  padding: 0.25rem 0.25rem;
  justify-content: center;
}
.col.flex{
  display: flex;
  justify-content: center;

  height: 100%;

}
.icon{
  background-color: grey;
  width: 2rem;
  height: 2rem;
}
.justify-end{
  justify-content: flex-end;
}
.align-center{
  align-items: center;
}
.flex-start{
  margin-right: auto
}

.key{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  margin-right: 0.25rem;
  margin-bottom: 0.25rem;
  border-style: none;
  border-radius: 4px;
  background-color: white;
  flex: 1;
  box-shadow: 
  0 1px 1px rgba(0,0,0,0.06), 
  0 2px 2px rgba(0,0,0,0.06), 
  0 4px 4px rgba(0,0,0,0.06), 
  0 8px 8px rgba(0,0,0,0.06);
}
.key:active{
  background-color: lightgrey;
  cursor: pointer;
}

.display{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  border: 4px solid black;
  border-right-width: 0px;
  background-color: white;
  aspect-ratio: 1 / 1;
  font-family: 'Sansita';
  font-weight: 900;
  font-size: 2.5rem;
  font-style: italic;
  text-transform: lowercase;
  cursor: pointer;
}
.display.current{
  background-color: #F5D1FD;
  
  
}

.display:first-child{
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.display:last-child{
  border-right-width: 4px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.display.flex.answer{
  background-color: yellow;
}
button{
  padding: 0.5rem 1rem;
  border-radius: 100px;
  border: 3px solid black;
  margin-right: 0.2rem;
  background-color: white;
  cursor: pointer;
}

.keyboard{
  position: absolute;
  top: 0;
}
.message{
  position: absolute;
  bottom: 0rem;
  padding: 1rem 1.5rem;
  border-radius: 1rem;
  border: 3px solid black;
  flex-direction: column;
  width: 33%;
  background-color: white;
  display: none;
}
.message.hint, .message.failed, .finished.completed{
  display: flex;
}
.finished{
  height:calc(100vh - 10rem);
  display: none;
}
.span.hint{
  background-color: blue;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.display.fin{
  background-color: #FFF2B1;
}
.wrapper.fin{
  background-color:#D5E8FF;
}
.center{
 
  align-items: center;
}

#video{
  border-radius: 0.5rem;
  border: 0.25rem solid black;
}
.container.desc{
  background-color: white;
  padding: 2rem;
  margin-top: 2rem;
  height: 80%;
  width: 100vw;
  max-width: 646px;
  border-radius: 1rem;
}
p{
  text-align: center;
}
